import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import { Helmet } from 'react-helmet'
import styled from 'styled-components'
import { getQueryPassword } from '../utils/passwordProtectUtils.js'

import Sidebar from './Sidebar'

const Container = styled.div`
  display: flex;
  height: 100vh;
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: scroll;
  padding: 20px;
  color: #7e7e7e;

  @media (min-width: 320px) and (max-width: 767px) {
    margin-top: 60px;
  }
`

const Query = graphql`
  query {
    site {
      siteMetadata {
        title
        authorName
      }
    }
  }
`

export default function Layout(props) {
  // Layout always loads, so attempt to set password if set
  try {
    // Second param set to true to save password if set
    getQueryPassword(window.location, true);
  } catch(e) {}

  return (
    <StaticQuery
      query={Query}
      render={data => {
        const { title, siteDescription, authorName } = data.site.siteMetadata
        return (
          <>
            <Helmet>
              <meta charSet="utf-8" />
              <meta name="description" content={siteDescription} />
              <title>{title}</title>
            </Helmet>
            <Container>
              <Sidebar title={title} authorName={authorName} />
              <Content>{props.children}</Content>
            </Container>
          </>
        )
      }}
    />
  )
}
