import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'
import {
  FaLinkedin,
  FaGithub,
  FaGithubAlt,
} from 'react-icons/fa'

import base64Images from '../utils/base64Images.json'

const SidebarWrapper = styled.div`
  /* Device = Tablets, iPads (portrait) */
  @media (min-width: 768px) and (max-width: 1024px) {
    width: 20%;
  }

  /* Device = Tablets, iPads (landscape) */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    width: 20%;
  }

  /* Device = Low resolution Tablets, Mobiles (landscape) */
  @media (min-width: 320px) and (max-width: 767px) {
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    top: 0;
    height: 60px;
    width: 100%;
    flex-basis: fit-content;
    flex-direction: row;

    &::before {
      content: '';
      margin: 10px;
    }
  }

  display: flex;
  flex-direction: column;
  width: 20%;
  background-color: #193549;
  color: #dcdcdc;
  justify-content: center;
  align-items: center;
`

const Logo = styled.div`
  img {
    /* Device = Tablets, iPads (portrait) */
    @media (min-width: 768px) and (max-width: 1024px) {
      width: 80px;
      height: 80px;
    }
  
    /* Device = Tablets, iPads (landscape) */
    @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
      width: 80px;
      height: 80px;
    }
  
    /* Device = Low resolution Tablets, Mobiles (landscape) */
    @media (min-width: 320px) and (max-width: 767px) {
      width: 50px;
      height: 50px;
      margin: auto;
      margin-top: 5px;
    }
  
    border-radius: 50%;
    overflow: hidden;
    width: 152px;
    height: 152px;
  }

  
  :hover {
    cursor: pointer;
    img {
      -webkit-animation-name: spinner; 
      -webkit-animation-timing-function: linear; 
      -webkit-animation-duration: .2s; 
      animation-name: spinner; 
      animation-timing-function: linear;
      animation-duration: .2s; 
      -webkit-transform-style: preserve-3d; 
      -moz-transform-style: preserve-3d; 
      -ms-transform-style: preserve-3d; 
      transform-style: preserve-3d;
      -webkit-transform-origin: center center;  /* Chrome, Safari */
      animation-fill-mode: forwards;
    }
  }
`

const StyledLink = styled(Link)`
  text-decoration: none;
  color: inherit;
  &:hover {
    transition: opacity .2s ease;
    opacity: .7;
    -webkit-text-decoration-color: inherit;
    text-decoration-color: inherit;
    cursor: pointer;
  }
`

const StyledLogoLink = styled(Link)``

const StyledHref = styled.a`
  text-decoration: none;
  color: inherit;
  &:hover {
    transition: opacity .2s ease;
    opacity: .7;
    -webkit-text-decoration-color: inherit;
    text-decoration-color: inherit;
    cursor: pointer;
  }
`

const MenuWrapper = styled.div`
  @media (min-width: 320px) and (max-width: 767px) {
    flex-direction: row;
    width: 100%;
    > p {
      margin: 0;
      margin-left: 7%;
    }
    div {
      width: 45%;
      margin-left: auto;
      p {
        margin: auto;
        padding-top: 3px;
      }
    }
  }

  @media (min-width: 768px) {
    svg {
      width: 50px;
      height: 1.2em;
    }
  }

  display: flex;
  flex-direction: column;
  text-align: center;

  p {
    font-size: 18px;
  }
  > div {
    display: flex;
    justify-content: space-between;
  }
`

const SiteTitle = styled.h2`
  display: flex;
  text-align: center;
  width: min-content;

  /* Device = Tablets, iPads (portrait) */
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 18px;
  }

  /* Device = Tablets, iPads (landscape) */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    font-size: 18px;
  }

  /* Device = Low resolution Tablets, Mobiles (landscape) */
  @media (min-width: 320px) and (max-width: 767px) {
    display: none;
  }
`

export default function Sidebar({ title, authorName }) {
  const src = base64Images['ninja-main'];
  return(
    <SidebarWrapper>
      <StyledLink to="/">
        <SiteTitle>{title}</SiteTitle>
      </StyledLink>
      <Logo>
        <StyledLogoLink to="/">
          <img
            src={src}
            alt={authorName}
          />
        </StyledLogoLink>
      </Logo>
      <MenuWrapper>
        <p>
          <StyledLink to="/about">
            <span>About</span>
          </StyledLink>
        </p>
        <p>
          <StyledLink to="/tags/cv">
            <span>Portfolio</span>
          </StyledLink>
        </p>
        <div>
          <p>
            <StyledHref
              href="https://github.com/PatBack118/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaGithubAlt title={'Personal Github'}/>
            </StyledHref>
          </p>
          <p>
            <StyledHref
              href="https://www.linkedin.com/in/patrick-mcmullen-49830585/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <FaLinkedin title={'Linkedin'}/>
            </StyledHref>
          </p>
          <p>
            <StyledHref
              href="https://github.com/patmcm/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaGithub title={'Work Github'}/>
            </StyledHref>
          </p>
        </div>
      </MenuWrapper>
    </SidebarWrapper>
  )
}
